



































































































































import { Vue, Component } from 'vue-property-decorator';
import { RegisterData } from '@/types/views/register.interface';

@Component
export default class Register extends Vue {
  tabItems!: object[]; // 选项卡
  activeTab!: number; // 当前选中的选项卡
  tabLoading!: boolean; // 选项卡内容加载中

  data(): RegisterData {
    return {
      tabItems: [], // 选项卡
      activeTab: 0, // 当前选中的选项卡
      tabLoading: false // 选项卡内容加载中
    };
  }

  // 路由跳转相关
  goToPage(to: any) {
    if (to.name) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.name === to.name) {
        return;
      }
      this.$router.push({
        name: to.name
      });
    } else if (to.path) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.path === to.path) {
        return;
      }
      this.$router.push({
        path: to.path
      });
    }
  }

  // 获取选项卡后台数据
  fetchTabItemsData() {
    // this.$api.userApi.register.getTabItems().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.tabItems = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    // this.tabItems = this.$api.userApi.register.getTabItems().data;
    this.tabLoading = true;
    setTimeout(() => {
      this.tabLoading = false;
      this.tabItems = this.$api.userApi.register.getTabItems().data;
    }, 1000);
  }

  // 生命周期钩子函数
  created() {
    // this.fetchTabItemsData();
    this.tabItems = this.$api.userApi.register.getTabItems().data;
  }
}
